p{
  margin: 0;
}
.search-container{
  margin-top: 70px;
}
.search-filter-top{
  background: white;
  border: 1px solid rgba(0,0,0,0.1);
  text-align: center;
  z-index: 2;
}
.search-filter-top li{
  list-style: none;
  display: inline-block;
  max-width: 100%;
  padding: 0 20px;
  line-height: 50px;
}
.search-filter-top li:hover a{
  border-bottom: 3px solid rgba(0,128,128,0.4);
}
.search-filter-top li a{
  font-size: 17px;
  font-weight: 500;
  text-decoration: none;
  color: rgba(0,128,128);
  padding: 5px;
  transition: all 0.2s ease;
}
.search-filter-top-active{
  border-bottom: 3px solid rgba(0,128,128,1);
}
.search-filter-sider h5{
  margin-bottom: 20px;
  white-space: pre-wrap;
  word-break: break-all;
}
.search-filter-sider h5 p {
  font-size: 16px;
  font-weight: 500;
  color: rgba(0,0,0,0.7);
  padding-right: 20px;
}
.search-filter-boss ul label , .search-filter-order ul label , .search-filter-time ul label {
  display: block;
}
.search-filter-boss ul label li, .search-filter-order ul label li, .search-filter-time ul label li{
  list-style: none;
  font-size: 14px;
  display: flex;
  align-items: center;
  padding: 1px 5px;
  border-radius: 1px;
  cursor: pointer;
}
.search-filter-boss ul label li:hover, .search-filter-order ul label li:hover, .search-filter-time ul label li:hover{
  background: rgba(0,0,0,0.1);
}
.search-filter-boss ul label li input, .search-filter-order ul label li input, .search-filter-time ul label li input{
  margin-right: 10px;
}
.filter-list-user, .filter-list-page, .filter-list-group, .filter-list-post{
  background: white;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 1px;
  padding: 5px 10px;
  margin-bottom: 10px;
}
.filter-list-user-item-container{
  position: relative;
  margin-top: 10px;
}
.filter-list-user-item{
  padding-bottom: 10px;
  display: flex;
}
.filter-list-user-item:last-child{
  border-bottom: 0 !important;
}
.filter-list-user-item-avatar{
  width: 70px;
  height: 70px;
  min-width: 70px;
  margin-right: 20px;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 50%;

}
.filter-list-user-item-avatar img{
  border-radius: 50%;
}
.filter-list-user-item-info p{
  font-size: 14px;
  padding-right: 100px;
  white-space: pre-wrap;
}
.filter-list-user-item-name a{
  font-size: 16px;
  text-decoration: none;
  font-weight: 600;
  color: rgba(0,128,128);
}
.filter-list-user-item-name a:hover{
  border-bottom: 1px solid rgba(0,128,128,0.3);
}
.filter-list-user-item-action{
  border: 1px solid rgba(0,0,0,0.1);
  background: #f5f6f7;
  display: inline-block;
  position: absolute;
  top: 0px;
  right: 2px;
  border-radius: 2px;
}
.filter-list-user-item-action button{
  border: 0 ;
  background: none;
  display: flex;
  align-items: center;
  padding: 2px 10px;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.2s ease;
}
.filter-list-user-item-action button:hover{
  background: #ebedf0;
}
.filter-list-user-item-action button:focus{
  outline: none;
}
.filter-list-user-item-action svg{
  padding: 2px;
  margin-right: 2px;
  font-size: 17px;
}
.filter-list-post-item-head{
  display: flex;
}
.filter-list-post-item-head-left{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 1px solid rgba(0,0,0,0.1);
  margin-right: 20px;
}
.filter-list-post-item-head-left img{
  border-radius: 50%;
}
.filter-list-post-item-head-right p{
  color: rgba(0,128,128);
  display: flex;
  align-items: center;
}
.filter-list-post-item-head-right p a{
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 600;
  color: rgba(0,128,128);
  text-decoration: none;
  border-bottom: 1px solid transparent;
}
.filter-list-post-item-head-right p a:hover{
  border-bottom: 1px solid rgba(0,128,128,0.3);
}
.filter-list-post-item-body{
  font-size: 14px;
  display: flex;
}
.filter-list-post-item-body-image{
  width: 150px;
  height: 150px;
  min-width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border: 1px solid rgba(0,0,0,0.1);
}
.filter-list-post-item-body p svg{
  margin: 0 5px;
}
.filter-list-post-item-footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  margin-top: 5px;
}
.filter-list-post-item-footer span{
  margin-left: 10px;
}
